<template>
  <div id="op-container" class="op-container" :style="{width,height,background}">
    <slot></slot>
  </div>
</template>

<script>
  import '@/assets/css/reset.css'
  import '@/assets/fonts/iconfont.css'
  import 'element-ui/lib/theme-chalk/index.css'
  export default {
    name: 'op-container',
    props: {
      background: {
        type: String,
        default: '#FFF'
      },
      width: {
        type: String,
        default: '100%'
      },
      height: {
        type: String,
        default: '100%'
      }
    }
  }
</script>

<style lang="less">
  // @import '../../assets/css/mixin.less';
  @import '../../assets/css/common.less';
  
  html, body {
    width: 100%;
    height: 100%;
  }

  .op-container {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
  }
</style>



