<template>
  <div class="login">
     <div style="margin-left:80px">
        <div class="login-logo">
        <img src="../../assets/img/logo1.png" alt="">
      </div>
      <div class="login-title">
        <h1>慧停车运营平台</h1>
        <p>慧停车——致力于使我们的城市更加美好！</p>
        <p>云端资源管理+信息实时共享停车服务全程优化</p>
      </div>
      <div class="login-form" >
        <el-form autoComplete="on" :inline="true" :model="loginForm" :rules="loginRules" ref="loginForm" label-position="left" label-width="0px" class="login-form-box">
          <el-form-item prop="username">
            <el-input name="username" type="text" v-model.lazy="loginForm.username" autoComplete="on" placeholder="用户名" />
          </el-form-item>
          <el-form-item prop="password">
            <el-input name="password" type="password" @keyup.enter.native="handleLogin" v-model.lazy="loginForm.password" autoComplete="on" placeholder="密码"></el-input>
          </el-form-item>
          <el-form-item prop="verifycode">
            <input type="text"  @keyup.enter="handleLogin" v-model.lazy="loginForm.verifycode" class="vertufyCode" placeholder="验证码"><img class="verifyImg" :src="verifyImg" @click="getVerifyCode('80', '50')">
          </el-form-item>
          <el-form-item style="width:100px">
            <el-button type="button" class="submit" :loading="loading" @click.prevent="handleLogin">
              登录
            </el-button>
          </el-form-item>
        </el-form>
    </div>
     </div>
  </div>
</template>
<script>
import { getVerifyCode } from '@/api/login'
export default {
  name: 'login',
  data () {
    const validatePass = (rule, value, callback) => {
      if (value.length < 6) {
        callback(new Error('密码不能小于6位'))
      } else {
        callback()
      }
    }
    const validVerifycode = (rule, value, callback) => {
      if (!value || value === '') {
        callback(new Error('验证码不能为空'))
        this.getVerifyCode('80', '50')
      } else if (this.verifycode !== this.loginForm.verifycode) {
        callback(new Error('验证码输入错误'))
        this.getVerifyCode('80', '50')
      } else {
        callback()
      }
    }
    return {
      loginForm: {
        username: '',
        password: '',
        verifycode: ''
      },
      verifycode: '',
      loginRules: {
        username: [{required: true, message: '请输入用户名', trigger: 'blur'}],
        password: [{required: true, trigger: 'blur', validator: validatePass}],
        verifycode: [{required: true, trigger: 'blur', validator: validVerifycode}]
      },
      loading: false,
      verifyImg: ''
    }
  },
  created () {
    this.getVerifyCode('80', '50')
  },
  methods: {
    async handleLogin () {
      const valid = await this.$refs.loginForm.validate()
      if (valid) {
        this.loading = true
        this.$store.dispatch('Login', this.loginForm).then(() => {
          this.loading = false
          location.href = '/home'
        }).catch(() => {
          this.loading = false
        })
      } else {
        return false
      }
    },
    async getVerifyCode (w, h) {
      let res = await getVerifyCode(w, h)
      this.verifycode = res.returnObject.code
      this.verifyImg = res.returnObject.binaryimage
    }
  }
}
</script>
<style lang='less'>
  .login{
    width: 100%;
    height: 100%;
    background: #348EDA url(../../assets/img/earth.png) no-repeat 100% 100%;
    &-logo{
    padding-top: 50px;
    }
    &-title{
      margin-top: 200px;
      margin-left: 100px;
      h1{
          color: #fff;
          font-weight: normal;
          font-size: 5rem;
      }
      p{
          color: #72B9E3;
          font-size: 1.2rem;
          line-height: 50px;
      }
    }
    &-form{
      margin-left:100px;
      &-box{
        .el-input__inner{
          width: 260px;
          height: 50px;  
        }
        .vertufyCode{
          width: 90px;
          height: 50px;
          padding-left: 10px;
          outline: none;
          border:none;
        }
        .vertufyCode:focus{
          border-color: #67c23a;
          outline-offset:0;
        }
        .verifyImg{
          width: 80px;
          height: 50px;
          background-color: #ddd;
          vertical-align: top;
          cursor: pointer;
        }
        .submit{
          border-radius: 2px;
          width: 140px;
          height: 50px;
          background: #5470C4;
          border: none;
          color: #fff;
          box-shadow: 0 0 10px #1F58A3;
          -moz-box-shadow: 0 0 10px #1F58A3;
          -webkit-box-shadow: 0 0 10px #1F58A3;
          cursor: pointer;
        }
      }
    }
  }
</style>





