import store from '@/store'
import { getToken } from '@/utils/auth' // getToken from cookie
 /**
 * 通过role.meanUrl判断是否与当前用户权限匹配
 * @param roles
 * @param path
 */
function hasPermission (roles, path) {
  if (roles && roles.length) {
    return roles.filter(role => role.meanUrl === path)
  }
}
export default function (router) {
  const whiteList = ['/login']// 白名单
  router.beforeEach((to, from, next) => {
    if (getToken()) {
      /* has token */
      const hasRoute = router.options.routes.filter(route => route.path === to.path)// 判断404
      if (hasRoute.length > 0) {
        if (to.path === '/login') {
          location.href = '/home'
        } else {
          const roles = store.getters.roles
          if (roles.length === 0) { // 判断当前用户是否已拉取完user_info信息
            store.dispatch('GetInfo').then(res => { // 拉取user_info
              const roles = res.returnObject
              const base = to.path
              store.dispatch('GenerateRoutes', { roles, base }).then(role => {
                if (role.length > 0) {
                  next()
                } else {
                  location.href = '/errorpage#/401'
                }
              })
            }).catch(() => {
              store.dispatch('FedLogOut').then(() => {
              // this.$message.error('Verification failed, please login again')
              location.href = '/login'
              next()
              })
            })
          } else {
           // 判断当前路由表中是否有此权限
            if (hasPermission(store.getters.roles, to.path)) {
              next()
            } else {
              location.href = '/errorpage#/401'
            }
          }
        }
      } else {
        location.href = '/errorpage#/404'
      }
    } else {
      /* has no token */
      const path = location.pathname
      if (whiteList.indexOf(path) !== -1) { // 在免登录白名单，直接进入
        next()
      } else {
        location.href = '/login'
        next() // 否则全部重定向到登录页
      }
    }
  })
}



