import Vue from 'vue'
import Router from 'vue-router'
import Login from 'components/login/index.vue'
Vue.use(Router)
export default new Router({
  /* 后端支持可开 */
  // mode: 'history',
  scrollBehavior: () => ({ y: 0 }),
  routes: [
    {path: '/', component: Login}
  ]
})



